                td Mnima, se requiere intervencin inmediata
                td Alta, requiere programacin detallada de tareas, adquisicin de recursos y asignacin de personal.
                td Alta, requiere monitoreo constante, anlisis de datos, interpretacin de tendencias y programacin de intervenciones.
                td Media, requiere programacin de chequeos peridicos y planes de contingencia.
              tr
                td.text-bold Costo Inicial
                td Bajo (solo materiales y mano de obra directamente relacionados con la reparacin)
                td Medio-Alto (recursos, capacitacin, implementacin de programas de mantenimiento)
                td Alto (sistemas de monitoreo, sensores, software, capacitacin especializada)
                td Bajo (inspecciones)
              tr
                td.text-bold Costo a Largo Plazo
                td Potencialmente alto (costos de reparacin imprevista, tiempo de inactividad, posible dao a otros componentes)
                td Bajo (reduce costos de reparaciones mayores, tiempo de inactividad y prdidas de produccin)
                td Bajo (previniendo fallas costosas, maximizando la vida til del equipo)
                td Variable, dependiendo de la frecuencia de chequeos y hallazgos en las inspecciones
              tr
                td.text-bold Riesgo de Paradas
                td Alto (paradas imprevistas frecuentes)
                td Bajo (paradas programadas y minimizadas)
                td Bajo (paradas minimizadas gracias a la prediccin de fallas)
                td Medio (solo si falla durante la operacin)
              tr
                td.text-bold Tiempo de Inactividad
                td Alto (reparacin inmediata suele ser necesaria)
                td Bajo (planificado, tiempo de inactividad mnimo)
                td Bajo (intervenciones programadas)
                td Bajo (intervenciones programadas)
              tr
                td.text-bold Tipos de Intervencin
                td Reparacin de la avera, reemplazo de componentes daados.
                td Inspeccin, lubricacin, limpieza, ajuste, reemplazo de componentes segn programa.
                td Monitoreo (vibraciones, termografa, anlisis de aceite, etc.), anlisis de datos, reemplazo preventivo de componentes.
                td Inspeccin visual, pruebas funcionales, reemplazo de componentes segn necesidad.
              tr
                td.text-bold Ventajas
                td Bajo costo si se aplica correctamente, sencillo de implementar.
                td Reduce fallas, planifica el mantenimiento, mejora la calidad, la seguridad y la disponibilidad.
                td Maximiza la confiabilidad, reduce prdidas de produccin, permite anlisis de fallas y optimiza el tiempo de inactividad.
                td Disponibilidad maximizada para equipos bajo demanda, reduce daos secundarios, aplicable a sistemas de seguridad.
              tr
                td.text-bold Desventajas
                td Alto riesgo de seguridad, grandes prdidas de produccin, posibles daos secundarios, poco control sobre el tiempo de la parada.
                td Costos incrementales, posibilidad de mantenimiento innecesario, riesgo de daos colaterales.
                td Costoso, requiere especialistas y tecnologa avanzada. Tiempo para anlisis e interpretacin de datos.
                td Subjetividad en las inspecciones, complejidad, requiere personal capacitado, poco control sobre el tiempo de la parada.

