    .row.justify-content-center.align-items-center.mb-4(data-aos="zoom-in-down")
      .col-lg-10.mb-3
        .align-items-center.d-flex.flex-nowrap.mb-0
          img.img-auto.pr-2(src='@/assets/curso/temas/tema2/img39.svg', alt='Imagen Decorativa')
          h3.mb-0 Efectividad del plan de mantenimiento:
      
      .col-lg-10.mb-4
        p A continuacin se presenta un enfoque estructurado para evaluar y mejorar los planes de mantenimiento de componentes o sistemas. Abarca la identificacin de fallas, la efectividad de las estrategias de mantenimiento y las consecuencias de las fallas. <b>Adems, proporciona recomendaciones para optimizar los procedimientos de mantenimiento y minimizar riesgos operativos:</b>

      .col-lg-10
        .titulo-sexto.color-acento-botones.mb-4
          h5 Tabla 5.
          span  Procedimientos de mantenimiento y minimizar riesgos operativos.
        .tabla-a.th_red.mb-2
          table
            thead
              tr
                th Paso
                th Descripcin
            tbody
              tr
                td.text-bold 1. Identificacin de modos de falla
                td 
                  span Identifica los modos de falla asociados con el componente o sistema.
              tr
                td.text-bold 2. Degradacin por edad
                td 
                  span Examina si existe una tasa significativa de degradacin por edad, si los materiales estn agotados, y si los modos de falla estn ocurriendo actualmente. <br>
                  span Capturar y mantener datos histricos ayuda a responder estas preguntas.
              tr
                td(rowspan="5").text-bold 3. Fallas funcionales
                td 
                  span Verificar si la falla funcional es evidente para el operador. Determine qu tipos de tareas se utilizan para identificar la falla:
              tr
                td  
                  span Mantenimiento peridico: Tareas sin considerar la condicin del equipo.
              tr
                td  
                  span Mantenimiento por condicin: Test o inspecciones segn la condicin del equipo.
              tr
                td  
                  span Ingeniera de confiabilidad: Uso de mantenimiento para detectar fallas no observadas.
              tr
                td  
                  span Identifica y mide los parmetros que reflejan el estado del sistema, como las vibraciones, y define tolerancias aceptables.
              tr
                td(rowspan="4").text-bold 4. Efectividad del plan de mantenimiento
                td 
                  span Evala la efectividad actual del mantenimiento. Identifica mtodos para mantener efectividad:
              tr
                td
                  span Mantenimiento peridico: Aumenta la probabilidad de falla tras el uso y restaura al estado original.
              tr
                td
                  span Mantenimiento por condicin: Caractersticas del modo de falla deben ser medibles y ofrecer amplio tiempo para intervencin.
              tr
                td
                  span Ingeniera de confiabilidad: Falla no es evidente y no existen tareas preventivas. Modifica o elimina tareas no efectivas.
              tr
                td(rowspan="4").text-bold 5. Consecuencias de la falla
                td 
                  span Identifica consecuencias relacionadas con seguridad, regulacin, produccin o costo, y si las tareas de mantenimiento agregan valor:
              tr
                td   
                  span Seguridad o regulacin: Reduccin de probabilidad de falla a nivel aceptable.
              tr
                td   
                  span Produccin: Reduccin del riesgo de falla a nivel aceptable.
              tr
                td   
                  span Costo: Costo de tareas preventivas menor que el costo de reparacin y prdida de capacidad.
              tr
                td(rowspan="9").text-bold 6. Recomendaciones para cambios
                td 
                  span <b>a.</b> Realiza las siguientes acciones segn los resultados del anlisis:
              tr
                td    
                  span <b>b.</b> Mantener procedimiento si es efectivo.
              tr
                td    
                  span <b>c.</b> Eliminar si es inefectivo.
              tr
                td    
                  span <b>d.</b> Modificar para efectividad.
              tr
                td    
                  span <b>e.</b> Cambiar la frecuencia de pruebas.
              tr
                td    
                  span <b>f.</b> Aadir un nuevo procedimiento.
              tr
                td    
                  span <b>g.</b> Cambiar forma de medir deterioro.
              tr
                td    
                  span <b>h.</b> Combinar procedimientos.
              tr
                td    
                  span <b>i.</b> Otras sugerencias.
                

    .row.justify-content-center.align-items-center.mb-4(data-aos="zoom-in-down")
      .col-lg-10
        .row.align-items-center
          .col-xl-3.col-md-6.col-lg-4.mx-auto.mb-3
            figure
              img(src='@/assets/curso/temas/tema2/img17.png', alt='Imagen Decorativa')
          .col-xl-9.col-lg-8
            p La gestin eficiente del mantenimiento industrial es crucial para la productividad, la rentabilidad y la seguridad de cualquier operacin manufacturera. Este proceso es una secuencia cuidadosamente orquestada de pasos que abarcan desde la identificacin proactiva de necesidades hasta la evaluacin exhaustiva de los resultados. 
            p Desde la deteccin temprana de posibles fallos hasta la implementacin de acciones correctivas, cada etapa juega un rol vital en la optimizacin de los recursos, la maximizacin de la vida til del equipo y la minimizacin de los tiempos de inactividad. A continuacin, se detalla un anlisis paso a paso de este proceso, destacando las mejores prcticas para asegurar un mantenimiento industrial eficiente y efectivo.
    
    .row.justify-content-center
      .col-lg-10
        PasosA.color-acento-contenido.mb-5(tipo="n")
          .row
            h3 I. Identificacin de necesidades (Requerimiento)
            .col-lg-12.mb-3
              .row.align-items-center
                .col-lg-7  
                  p.mb-3 Esta etapa crucial inicia el proceso de mantenimiento. Se determina la demanda de servicios de mantenimiento a travs de mltiples fuentes, identificando qu equipos o sistemas requieren atencin y el tipo de mantenimiento necesario.
                .col-md-6.col-lg-5
                  figure(data-aos="zoom-in")
                    img(src='@/assets/curso/temas/tema2/img18.png', alt='').img125

            .col-lg-12
              p <b>Recopilacin de datos:</b> Consultar manuales del fabricante, registros histricos de mantenimiento, reportes de operadores (quejas, sugerencias), inspecciones regulares o programadas, anlisis de datos histricos (identificacin de tendencias y problemas recurrentes), polticas de abastecimiento de materiales y repuestos, evaluacin de actualizaciones de equipos. Priorizacin de necesidades.

          .row
            h3.text-start II. Planificacin del mantenimiento
            .col-lg-12.mb-3
              .row.align-items-center
                .col-md-6.col-lg-5.mb-3
                  figure(data-aos="zoom-in")
                    img(src='@/assets/curso/temas/tema2/img19.png', alt='').img125
                .col-lg-7  
                  p.mb-3.text-start Se define qu tareas de mantenimiento se realizarn para satisfacer las necesidades identificadas en la etapa anterior. Se considera la disponibilidad de recursos (personal, materiales, tiempo y presupuesto) para asegurar la compatibilidad entre los requerimientos y la capacidad de ejecucin.

            .col-lg-12
              p.text-start <b>Creacin de un listado detallado de requerimientos de mantenimiento.</b> Anlisis exhaustivo de planificaciones anteriores, incluyendo la identificacin de xitos, fracasos y reas de mejora (retroalimentacin). Definicin y seguimiento de indicadores clave de rendimiento (KPIs) para medir la eficiencia del mantenimiento. Asignacin de recursos (personal, materiales, presupuesto).

          .row
            h3 III. Programacin del mantenimiento
            .col-lg-12.mb-3
              .row.align-items-center
                .col-lg-7  
                  p.mb-3 Se crea un programa detallado que especifica las actividades de mantenimiento, asignando recursos, tiempos especficos y prioridades a cada tarea. Se busca optimizar la eficiencia y minimizar el tiempo de inactividad.
                .col-md-6.col-lg-5
                  figure(data-aos="zoom-in")
                    img(src='@/assets/curso/temas/tema2/img20.png', alt='').img125

            .col-lg-12
              p <b>Elaboracin de un cronograma preciso.</b> Asignacin especfica de personal y equipos a cada tarea. Definicin clara de los procedimientos a seguir para cada tarea de mantenimiento. Obtencin de los materiales y herramientas necesarios. Revisin y anlisis de programas de mantenimiento anteriores, e incorporacin de mejoras e innovaciones.

          .row
            h3.text-start IV. Ejecucin del mantenimiento
            .col-lg-12.mb-3
              .row.align-items-center
                .col-md-6.col-lg-5.mb-3
                  figure(data-aos="zoom-in")
                    img(src='@/assets/curso/temas/tema2/img21.png', alt='').img125
                .col-lg-7  
                  p.mb-3.text-start Se llevan a cabo las tareas de mantenimiento programadas. Se verifica el correcto uso de herramientas y materiales, se documentan las acciones realizadas y se resuelven los problemas imprevistos que puedan surgir durante la ejecucin. Se coordina la ejecucin con la produccin para minimizar las interrupciones.

            .col-lg-12
              p.text-start <b>Distribucin eficiente del trabajo</b> entre el personal de mantenimiento y coordinacin con el rea de produccin para determinar el mejor momento para la intervencin. Monitoreo constante del progreso del trabajo. Movilizacin de recursos (personal, herramientas, repuestos). Ejecucin precisa de las tareas segn los procedimientos establecidos. Documentacin exhaustiva de todas las acciones realizadas. Resolucin efectiva de problemas imprevistos.

          .row
            h3 V. Finalizacin y entrega
            .col-lg-12.mb-3
              .row.align-items-center
                .col-lg-7  
                  p.mb-3 Una vez completadas las tareas, se realizan pruebas exhaustivas para asegurar la confiabilidad y el correcto funcionamiento del equipo antes de ser devuelto al operador.
                .col-md-6.col-lg-5
                  figure(data-aos="zoom-in")
                    img(src='@/assets/curso/temas/tema2/img22.png', alt='').img125

            .col-lg-12
              p <b>Realizacin de pruebas</b> de funcionamiento en vaco y bajo carga. Medicin de variables clave para asegurar que el equipo cumple con los estndares de rendimiento. Anlisis del comportamiento del equipo por personal experto. Diseo y realizacin de experimentos para verificar la eficiencia del equipo. Ajustes y calibraciones finales. Documentacin de resultados de las pruebas y ajustes realizados. Entrega formal del equipo al operador.
                
          .row
            h3.text-start VI. Control, evaluacin y mejora continua
            .col-lg-12.mb-3
              .row.align-items-center
                .col-md-6.col-lg-5.mb-3
                  figure(data-aos="zoom-in")
                    img(src='@/assets/curso/temas/tema2/img23.png', alt='').img125
                .col-lg-7  
                  p.mb-3.text-start Se compara el trabajo realizado con lo planificado inicialmente. Se analizan las desviaciones, se identifican las causas raz de cualquier problema y se implementan acciones correctivas para mejorar la eficiencia y la eficacia del proceso de mantenimiento en futuras intervenciones.

            .col-lg-12
              p.text-start <b>Recopilacin de datos sobre el trabajo realizado</b> (tiempo, costo, materiales utilizados, etc.). Comparacin de los datos con el plan inicial. Anlisis de indicadores de desempeo para identificar reas de mejora. Identificacin de las causas raz de cualquier desviacin o problema. Implementacin de acciones correctivas para prevenir problemas futuros. Documentacin de los resultados de la evaluacin y acciones correctivas. Mejora continua del proceso.


    .row.justify-content-center.align-items-center.mb-4(data-aos="zoom-in-down")
      .col-lg-10.mb-3
        .align-items-center.d-flex.flex-nowrap.mb-0
          img.img-auto.pr-2(src='@/assets/curso/temas/tema2/img39.svg', alt='Imagen Decorativa')
          h3.mb-0 El ciclo virtuoso:

      .col-lg-10.mb-3
        p.mb-2 Un ciclo virtuoso es un conjunto de eventos que se refuerza a travs de un circuito de retroalimentacin. Un ciclo virtuoso tiene resultados favorables. Un ciclo virtuoso puede transformar en un ciclo vicioso, si se tiene en cuenta la retroalimentacin negativa final.
        p Ambos crculos son complejos de eventos con ninguna tendencia hacia el equilibrio (al menos en el corto plazo). Ambos sistemas de eventos tienen ciclos de retroalimentacin en el que cada iteracin del ciclo refuerza la primera (retroalimentacin positiva). Estos ciclos se mantendrn en la direccin de su impulso hasta que un factor externo interviene y rompe el ciclo.

      .col-lg-10.mb-3
        h4.mb-3 A continuacin, algunos de los principales elementos del ciclo virtuoso:

        ImagenInfografica.color-acento-contenido
