          .tarjeta.bg-white.p-3(x="5.7%" y="47.2%" numero="1")
            .h5.mb-2 Planificacin y estrategia.
            p.mb-2  <b>Anlisis de la Situacin Actual:</b> Evaluar el estado actual de los procesos, equipos, recursos y resultados. Identificar reas de mejora y problemas existentes.
            p <b>Definicin de Objetivos:</b> Establecer metas claras y alcanzables basadas en el diagnstico inicial, como mejorar la eficiencia, reducir costos, o aumentar la calidad.

          .tarjeta.bg-white.p-3(x="26%" y="30%" numero="2")
            .h5.mb-2 Planificacin y estrategia.
            p.mb-2  <b>Desarrollo de Estrategias: </b> Crear un plan para abordar las reas identificadas para la mejora. Esto puede incluir la implementacin de nuevas tecnologas, cambios en los procesos o capacitacin del personal.
            p <b>Asignacin de Recursos:</b>  Determinar los recursos necesarios, como personal, tecnologa, y materiales, para llevar a cabo las estrategias planificadas.

          .tarjeta.bg-white.p-3(x="41%" y="54%" numero="3")
            .h5.mb-2 Implementacin
            p.mb-2  <b>Ejecutar el Plan:</b>  Poner en prctica las estrategias y mejoras diseadas. Esto puede implicar la instalacin de nuevos equipos, la modificacin de procesos o la formacin de los empleados.
            p <b>Monitoreo y Control:</b>  Supervisar la implementacin para asegurar que se realice segn lo planificado y que los cambios estn produciendo los resultados esperados.

          .tarjeta.bg-white.p-3(x="52%" y="29%" numero="4")
            .h5.mb-2 Evaluacin de resultados.
            p.mb-2  <b>Medicin del Rendimiento:</b>  Evaluar el impacto de las mejoras implementadas. Esto se realiza a travs de indicadores clave de rendimiento (KPI) y otros mtodos de evaluacin.
            p <b>Anlisis de Desviaciones:</b> Identificar cualquier desviacin de los objetivos y entender las razones detrs de cualquier problema o desafo.

          .tarjeta.bg-white.p-3(x="58.7%" y="65.2%" numero="5")
            .h5.mb-2 Retroalimentacin y ajuste.
            p.mb-2  <b>Revisin y Ajuste:</b>  Basado en los resultados evaluados, realizar ajustes y mejoras adicionales. Esto puede implicar corregir problemas, ajustar estrategias, o implementar nuevas medidas para optimizar an ms los procesos.
            p <b>Lecciones Aprendidas:</b>  Documentar y compartir las lecciones aprendidas para aplicar en futuros ciclos.

          .tarjeta.bg-white.p-3(x="76.7%" y="35.2%" numero="6")
            .h5.mb-2 Consolidacin y Estandarizacin
            p.mb-2  <b>Estandarizacin de Mejores Prcticas:</b>  Asegurar que las mejoras efectivas se integren en los procedimientos estndar de operacin.
            p <b>Consolidacin de Cambios:</b> Integrar los cambios en la cultura y las prcticas operativas de la organizacin para mantener los beneficios alcanzados.
          .tarjeta.bg-white.p-3(x="86.7%" y="72.2%" numero="7")
            .h5.mb-2 Innovacin continua.
            p.mb-2  <b>Fomentar la Innovacin:</b>   Promover un entorno en el que se busque continuamente nuevas formas de mejorar y optimizar. Esto incluye mantenerse al da con las tendencias tecnolgicas y las mejores prcticas de la industria
            p <b>Repeticin del Ciclo:</b>  Repetir el ciclo virtuoso con una nueva ronda de diagnstico y evaluacin, continuando as el proceso de mejora continua.

