          h3.mb-0 Herramientas para el control de calidad y mejoramiento del mantenimiento:

    .row.justify-content-center.align-items-center.mb-3(data-aos="zoom-in-down")
      .col-lg-8.col-md-12.mb-3
        p.mb-3 El desarrollo de un sistema de control de calidad para el mantenimiento es esencial para asegurar alta calidad de la reparacin,  afinar  la  estandarizacin,  maximizar  la disponibilidad, extender la vida econmica del activo y asegurar una alta eficiencia y tasa de produccin del equipo. 
        p La responsabilidad del grupo de control de calidad incluye el desarrollo de procedimientos para pruebas, inspecciones y ejecucin del trabajo, documentacin, seguimientos o monitoreo, anlisis de las deficiencias, e identificacin de las necesidades de entrenamiento a partir del anlisis de los reportes de calidad. 
      .col-lg-2.col-md-6.mb-3
        figure(data-aos="zoom-in")
