        p.mb-3 Una organizacin para el mantenimiento no puede olvidar que su participacin en el logro de las metas de la empresa es crtica ya que su foco es la alta disponibilidad de sus activos. Por tanto, debe vincular sus objetivos con los de la empresa y entregarlos con la ms alta calidad.
        p Hay que tener mucho cuidado en la recoleccin de los datos para que sean compatibles con el fin que se persigue y que adems sean completos para la aplicacin de la herramienta escogida. 

    
