          h3.mb-0 Histograma de frecuencia relativa:

    .row.justify-content-center.align-items-center.mb-3(data-aos="zoom-in-down")
      .col-lg-10.mb-2
        p La gestin eficiente del mantenimiento industrial requiere una comprensin profunda de la fiabilidad de los equipos. A diferencia de lo que se podra suponer, las piezas de un equipo, incluso siendo similares, no presentan una vida til idntica. No todas fallarn al mismo tiempo ni tras el mismo nmero de horas de funcionamiento. Esta variabilidad es fundamental para una planificacin efectiva del mantenimiento.
        
      
    .row.justify-content-center
      .col-lg-8.col-xl-6.mb-3
        figure
          img.img-auto(src='@/assets/curso/temas/tema2/img29.png', alt='Imagen Decorativa')
      .col-lg-10.col-xl-4.mb-2
        p Para cuantificar esta variabilidad y predecir la probabilidad de fallo, se utiliza la construccin de un histograma de frecuencia de fallas. En este histograma, el eje horizontal representa el tiempo de funcionamiento del equipo (o de un componente especfico), y el eje vertical representa la frecuencia con la que se producen las fallas en cada intervalo de tiempo. Cada barra del histograma representa un intervalo de tiempo, y su altura indica la cantidad de fallas ocurridas dentro de ese intervalo. El rea total bajo la curva del histograma representa la totalidad de las fallas observadas.

    .row.justify-content-center.align-items-center.mb-4(data-aos="zoom-in-down")
      .col-lg-10.mb-3
        p La probabilidad de que una falla ocurra dentro de un intervalo de tiempo especfico (t a t) se puede calcular multiplicando la frecuencia relativa de fallas en ese intervalo (obtenida del histograma) por la amplitud del intervalo. La frecuencia relativa, o ordinal, se calcula dividiendo el nmero de fallas en un intervalo especfico por el nmero total de fallas observadas.

      .col-lg-10.mb-3
        p Es importante destacar que la suma de las probabilidades de fallo en todos los intervalos de tiempo considerados (desde el tiempo inicial t hasta el tiempo final t) debe ser igual a 1, lo que representa la certeza de que alguna falla ocurrir en algn momento.

    .row.justify-content-center.align-items-center.mb-2
      .col-lg-10.mb-3
        .bloque-texto-g.bloque-texto-g.bg02.p-3.p-sm-4.p-md-5
          .bloque-texto-g__img(
            :style="{'background-image': `url(${require('@/assets/curso/temas/tema2/img30.png')})`}"
          )
          .bloque-texto-g__texto.p-4
            p.mb-0 Para obtener resultados confiables, es crucial que el nmero de observaciones (cantidad de fallas registradas) sea suficiente para que la distribucin de frecuencias se ajuste a los modelos probabilsticos apropiados. Este nmero se determina mediante criterios estadsticos, garantizando as la validez del anlisis.

      .col-lg-10.mb-3
        p Esta metodologa, basada en datos empricos, proporciona una estimacin precisa de la probabilidad de fallo en diferentes intervalos de tiempo. Esta informacin resulta esencial para optimizar las estrategias de mantenimiento preventivo y predictivo, lo que permite reducir los tiempos de inactividad, minimizar los costos de reparacin y mejorar la eficiencia general de la operacin.

