          h3.mb-0 Funcin densidad de probabilidad: 

    .row.justify-content-center.align-items-center.mb-3
      .col-lg-10
        p.mb-3 En los estudios de mantenimiento se tiende a usar la funcin de densidad de probabilidad (f(t)) ms bien que los histogramas de frecuencia relativa. Esto porque: 
